import React from 'react'

import styled from 'styled-components';

const Button = styled.a`
    display: inline-block;
    color: white;
    font-family: 'TT-Norms', sans-serif;
    font-size: .75rem;
    background-color: black;
    padding: 1rem 2.5rem;
    text-transform: uppercase;
    text-decoration: none;
    margin-top: 1.5rem;
`;

export class CTA extends React.Component {

    onClick(ev) {
        ev.preventDefault();
        var form = document.getElementsByClassName('_form-wrapper');
        if (form.length) {
            form[0].style.display = 'block';
        }
        // var script = document.createElement('script');
        // script.setAttribute('src', 'https://mariacarvajalcasado.activehosted.com/f/embed.php?id=1');
        // document.head.appendChild(script);
    }

    render() {
        return <Button href="#" onClick={this.onClick.bind(this)}>{this.props.children}</Button>;
    }
}